<template>
  <div class="todo-item" v-bind:class="{'is-complete':todo.completed}"> 
    <p>
      <input type="checkbox" v-on:change="markComplete">
      {{todo.title}}
      <button @click="$emit('del-todo', todo.id)" class="del" >x</button>
      <button @click="$emit('update-todo', todo.id, todo.completed)" class="update"> Update</button>
    </p>
     
    
  </div>
</template>
 

<script>
export default {
    name: "TodoItem",
    props: ["todo"],
    methods: {
      markComplete() {
        //   console.log(123)
        this.todo.completed = !this.todo.completed
      }  
    }
}

</script>


<style scoped>
.todo-item {
    background: #f4f4f4;
    padding: 10px;
    border-bottom: 1px #ccc dotted;
  }
  .is-complete {
    text-decoration: line-through;
  }
  .del {
    background: #ff0000;
    color: #fff;
    border: none;
    padding: 5px 9px;
    border-radius: 50%;
    cursor: pointer;
    float: right;
  }
  .update {
    background: #00ff00;
     float: right;
     margin-right: 5px;
     cursor: pointer;
     padding: 5px 9px;
  }
</style>