<template>
  <div>
    <!-- <form @submit.prevent="addTodo"> -->
    <form @submit="addTodo">
      <input type="text" v-model="title" name="title" placeholder="Add Todo...">
      <input type="submit" value="Submit" class="btn">
    </form>
  </div>
</template>


<script>
// import {v4 as uuid} from 'uuid';

export default {
  name: "AddTodo",
  data() {
    return {
      title: '',
      completed: false
    }
  },
  methods: {
    addTodo(e) {
      e.preventDefault();  
      const newTodo = {
        title: this.title,
        completed: this.completed
      }
      // Send up to parent
      this.$emit('add-todo', newTodo);  

      this.title = '';
    }
  }  
}
</script>



<style scoped>
  form {
    display: flex;
  }

  input[type="text"] {
    flex: 10;
    padding: 5px;
  }

  input[type="submit"] {
    flex: 2;
  }

</style>